<template>
  <div class="login-register">
    <div class="explain"></div>
    <div class="top">
      <div class="content">
        <h4 class="mi-title">欢迎登录小米有品</h4>

        <!-- 登录框 -->
        <div class="login" v-show="loginandregister">
          <div class="inpu">
            <input
              type="text"
              placeholder="邮箱/手机号码/小米ID"
              v-model.trim="loginnumber"
            />
          </div>
          <div class="inpu">
            <input
              type="password"
              placeholder="密码"
              v-model.trim="loginpassword"
            />
          </div>
          <div class="login-buttom" @click="login">登录</div>
        </div>

        <!-- 注册框 -->
        <div class="register" v-show="!loginandregister">
          <div class="inpu">
            <input
              type="text"
              placeholder="手机号码"
              v-model.trim="redisternumber"
            />
          </div>
          <div class="inpu">
            <input
              type="text"
              placeholder="注册密码"
              v-model.trim="redisterpassword"
            />
          </div>
          <div class="register-buttom" @click="register">立即注册</div>
        </div>
        <div class="switchover" @click="switchover">{{ switchovers }}</div>
        <div class="way">
          <van-divider
            :style="{
              color: '#666',
              borderColor: '#1989fa',
              padding: '0 16px',
            }"
          >
            其他登录方式
          </van-divider>
        </div>
        <div class="mode">
          <div class="mode-one"><a href="#"></a></div>
          <div class="mode-two"><a href="#"></a></div>
          <div class="mode-three"><a href="#"></a></div>
          <div class="mode-fore"><a href="#"></a></div>
        </div>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import "../../assets/sass/LoginAndRegister.scss";
export default {
  data() {
    return {
      loginandregister: true,
      switchovers: "手机号码注册",
      // 注册号码
      redisternumber: "",
      // 注册密码
      redisterpassword: "",
      // 登录号码
      loginnumber: "",
      // 登录密码
      loginpassword: "",

      leave:'',
    };
  },

  methods: {
    switchover() {
      this.loginandregister = !this.loginandregister;
      this.switchovers =
        this.loginandregister == true ? "手机号码注册" : "登录";
    },
    login() {
      let data1 = JSON.parse(sessionStorage.getItem("key"));
      
      data1.forEach((item) => {
        if (
          item.redisternumber == this.loginnumber &&
          item.redisterpassword == this.loginpassword
        ) {
          
          // 成功通知
          this.$notify({ type: "success", message: "登录成功" });
          sessionStorage.setItem("loginstate", "true");
          sessionStorage.setItem("loginnickname", this.loginnumber);
          this.$router.go(-1);
        } else {
          this.$dialog
            .alert({
              message: "账号或密码错误",
            })
            .then(() => {
              // on close
            });
          
        }
      });
    },
    register() {
      // 
      // 
      if (this.redisternumber == "" || this.redisterpassword == "") {
        this.$dialog.alert({
          message: "请输入注册账号或密码",
        });
        
      } else {
        let data2 = JSON.parse(sessionStorage.getItem("key"));
        data2.push({
          redisternumber: this.redisternumber,
          redisterpassword: this.redisterpassword,
        });
        sessionStorage.setItem("key", JSON.stringify(data2));
        
        this.loginandregister = !this.loginandregister;
        this.$notify({ type: "success", message: "注册成功" });
      }
    },
  },
};
</script>